.sidebarContainer {
  display: flex;
  flex: 0 0 $sidebarWidth;
  flex-direction: column;
  margin: 0 8px 8px;
  width: $sidebarWidth;
  height: calc(100% - 8px);
  border-radius: 4px;
  background-color: $blackDarkTranslucentBackgroundColor;
  /*
    Fixes Safari rendering glitch
    See: https://github.com/plexinc/plex-web-client/issues/13103
  */
  transform: translate3d(0, 0, 0);
}
