.modal {
  composes: medium from 'app/js/v4/core/ui/modals/components/Modal.css';

  width: 780px;
  height: 720px;
}

.modalBody {
  composes: modalBody from 'app/js/v4/core/ui/modals/components/ModalBody.css';

  display: flex;
  flex-grow: 1;
  padding: 0;
  width: 100%;
}

.innerModalBody {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 100%;
}

.scroller {
  composes: scroller from 'app/js/v4/core/ui/scrollers/components/VirtualListScroller.css';

  flex-grow: 1;
}

.innerContainer {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-grow: 1;
  padding: $modalBodyPadding 0;
}

.lineupsContainer {
  composes: container from 'app/js/v4/features/dvrManagement/components/setup/mapping/EPGLineups.css';
}

.lineupsLabel {
  color: $v4AltMedium;
}

.lineups {
  composes: lineups from 'app/js/v4/features/dvrManagement/components/setup/mapping/EPGLineups.css';
}

.lineup {
  composes: lineup from 'app/js/v4/features/dvrManagement/components/setup/mapping/EPGLineups.css';
}

.buttonBar {
  composes: buttonBar from 'app/js/v4/core/ui/modals/components/ModalFooterButtonBar.css';

  display: flex;
  justify-content: space-between;
  width: 100%;
}
