.container {
  display: flex;
  flex-direction: column;
  padding: 10px;
  width: 200px;
  border-radius: $liveTVSettingsBorderRadius;
  background-color: $liveTVSettingsBackground;
}

.action {
  composes: link from 'app/js/v4/core/ui/buttons/components/Link.css';

  margin-bottom: 10px;
}

.spinner {
  composes: spinner from 'app/js/v4/core/ui/spinners/components/Spinner.css';

  margin-right: 8px;
}

.spinnerContainer {
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-bottom: 10px;
  color: $v4AltMedium;
}

.actionIcon {
  margin-right: 8px;
  margin-bottom: -1px;
  font-size: $defaultIconFontSize;
}

@media only screen and (max-width: $breakpointLarge) {
  .container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: row;
    width: 100%;
  }

  .action,
  .spinnerContainer {
    margin-bottom: 0;
  }
}
