.container {
  position: relative;
  height: 386px;
}

.listActions {
  display: flex;
  flex-shrink: 0;
  font-size: 16px;
}

.listScrollButton {
  composes: iconButton from 'app/js/v4/core/ui/buttons/components/IconButton.css';

  display: flex;

  &:not(:first-child) {
    margin-left: 14px;
  }

  &:global(.isDisabled) {
    opacity: 0.15;
  }
}
