.row {
  position: relative;
  min-height: 44px;
  color: $defaultLinkColor;

  &:hover {
    color: $defaultColor;
  }
}

.alternateRow {
  composes: row;
}

.underlay {
  @add-mixin cover;
  composes: link from 'app/js/v4/core/ui/buttons/components/Link.css';
}

.overlay {
  @add-mixin cover;
  display: flex;
  align-items: center;
  pointer-events: none;
}

.iconContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 20px;
  width: 20px;
}

.title {
  @add-mixin doubleLineTruncate;
  flex-grow: 1;
  pointer-events: all;

  &:hover {
    color: $defaultColor;
    text-decoration: underline;
  }
}

.actions {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 1;
  opacity: 0;
}

.action {
  @add-mixin doubleLineTruncate;
  composes: link from 'app/js/v4/core/ui/buttons/components/Link.css';

  flex-shrink: 0;
  padding: 0 15px;
  white-space: nowrap;
  pointer-events: all;
}

.visible {
  opacity: 1;
}

.editIcon {
  margin-right: 5px;
}

.header {
  display: flex;
}

.columnHeader {
  @add-mixin doubleLineTruncate;

  align-self: flex-end;
  padding: 0 5px;
  width: 100px;
  text-align: center;
}

.headerIcon {
  display: flex;
  justify-content: center;
  width: 20px;
}

.upgradeText {
  opacity: 0;
}

.upgradeTextHovered {
  composes: upgradeText;

  opacity: 1;
}
