.topContainer {
  display: flex;
  height: 76px;
}

.avatarImg {
  composes: avatar from 'app/js/views/v3/components/core/images/AvatarImg.css';

  flex-shrink: 0;
  margin-right: 30px;
  width: 76px;
  height: 76px;
}

.description {
  color: $dimColor;
}

.modalFooter {
  composes: modalFooter from 'app/js/v4/core/ui/modals/components/ModalFooter.css';

  justify-content: center;
  flex-direction: column;
  height: 224px;
}

.formLabel {
  composes: label from 'app/js/views/v3/components/core/forms/FormLabel.css';

  margin-bottom: 10px;
  width: 100%;
}

.form {
  margin-bottom: 15px;
  width: 100%;
}

.textArea {
  composes: textArea from 'app/js/views/v3/components/core/forms/TextArea.css';

  max-width: none;
}

.buttonBar {
  composes: buttonBar from 'app/js/v4/core/ui/modals/components/ModalFooterButtonBar.css';

  display: flex;
  justify-content: flex-end;
  width: 100%;
}
