.addDeviceButton {
  padding: 7px;
  width: $liveTVSettingsDeviceWidth;
  border-width: 0 0 0 $liveTVSettingsDeviceBorderSize;
  border-style: solid;
  border-color: $v4AltMediumDark;
  border-radius: $liveTVSettingsBorderRadius;
  background-color: $liveTVSettingsAddDeviceButtonBackground;
  color: $v4AltMedium;
  text-align: left;
  text-transform: uppercase;
  font-size: 11px;
  font-family: $defaultFontFamily;
}

.addDeviceButtonIcon {
  margin-right: 7px;
  font-size: 19px;
}

.unsupportedInfo {
  padding: 7px;
  width: $liveTVSettingsDeviceWidth;
  border-color: $v4AltMediumDark;
  border-radius: $liveTVSettingsBorderRadius;
  background-color: $liveTVSettingsAddDeviceButtonBackground;
  color: $v4AltMedium;
  font-size: 11px;
}

@media only screen and (max-width: $breakpointLarge) {
  .container {
    width: 100%;
  }

  .addDeviceButton {
    margin-right: 0;
    width: 100%;
  }

  .unsupportedInfo {
    width: 100%;
  }
}
