.row {
  display: flex;
  padding-left: 40px;
  width: 100%;
  height: 40px;

  &:hover {
    background-color: $stripeListHoverBackgroundColor;
    .delete {
      opacity: 1;
    }
  }
}

.draggingRow {
  composes: row;

  background-color: $stripeListSelectedBackgroundColor;
}

.isActive {
  .delete {
    opacity: 1;
  }
}

.handle {
  display: flex;
  align-items: center;
  padding: 10px;
}

.title {
  display: flex;
  align-items: center;
  padding: 10px;
  min-width: 0;
  width: 100%;
}

.titleText {
  @add-mixin truncate;
}

.inputs {
  display: flex;
  align-items: stretch;
  justify-content: flex-end;
  flex: 1;
}

.input {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;

  input[type='checkbox']:focus {
    outline: auto;
  }
}

.delete {
  display: flex;
  align-items: center;
  width: 50px;
  opacity: 0;
}
