.secureIcon {
  color: $successColor;
}

.alertIcon {
  color: $dangerColor;
}

.warningIcon {
  color: $warningColor;
}

/* Ideally we should not need to 'badge' the icons. We should eventually have
 * proper filled badged icons that have no transparent parts. Until then, we
 * force badge effect. */

.circleBadge {
  display: flex;
  border-radius: 50%;
  background-color: #000;
}

.triangleBadge {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background-color: #000;
}
